import React, { lazy, Suspense } from "react"
import { BrowserRouter, Routes, Route, Link } from 'react-router-dom';
import './App.css';
import Header from './components/header';
//lazy loading & suspense for better perf and laoding times.

// Live games & game - 
const BoxScores = lazy(() => import('./components/boxscore'));
const OneScore = lazy(() => import('./components/pastgames'));

//Upcoming Games & Game --

const UpcomingGames = lazy(() => import('./components/upcomingGames'));
const UpcomingGame = lazy(() => import('./components/upcomingGame'));


//Finished Games & Game --

const FinishedGames = lazy(() => import('./components/finGames'));
//const FinishedGame = lazy(() => import('./components/finGame'));






const renderLoader = () => <h1 className="has-text-centered	h2uk">Loading..</h1>;



function App() {
  return (
    <BrowserRouter>
      <div className="App content">
        <Header />
        <br /><br />

        <Suspense fallback={renderLoader()}>
          <Routes>
            <Route path="boxscore" element={<BoxScores/>} />
            <Route path="/boxscore/:slug" element={<OneScore />} />


            <Route path="upcomingGames/" element={<UpcomingGames />} />
            <Route path="/upcomingGames/:slug" element={<UpcomingGame />} />
            <Route path="finGames" element={<FinishedGames />} />
          </Routes>

        </Suspense>



      </div>


    </BrowserRouter>





  );
}

export default App;